@import "./../../../../Styles/Variable";
@import "./../../../../Styles/Mixin.scss";
@import "./../../../../Styles/Mixin_New.scss";

.About-wrapper {
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;

    @include respond-to(xs) {
        height: auto;
    }

    .About-div {
        flex-basis: 880px;
        height: 410px;
        background-color: $bg-div;
        display: flex;
        border-radius: 0.5rem;
        border: 1px solid $link;
        // margin: 1rem;

        @include respond-to(xs) {
            flex-direction: column;
            height: auto;

        }


        .About-title {
            flex: 1;
            text-align: left;
            padding: 0.5rem;
            border-right: 1px solid $link;

            @include respond-to(xs) {
                border-right: 1px solid transparent;
            }

            // background: red;

            .About-title-btn-div {
                display: flex;
                flex-direction: column;
                border: none;

                .btnlink {
                    height: 30px;
                    border: none;
                    margin: 0.3rem 0;
                    border-radius: 5px;
                    text-align: left;
                    padding-inline-start: 10px;
                    background: none;
                    color: #a9acb1;
                    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                    font-weight: 500;

                    &:hover {
                        background-color: $border-div;
                    }
                }
            }

            .About-title-text {
                font-size: 20px;
                font-weight: 600;
                margin-left: 10px;

            }
        }

        .abot-hr {
            display: none;

            @include respond-to(xs) {
                display: flex;
            }
        }

        .About-details {
            flex: 2;
            padding: 1rem 2rem;
            text-align: left;

            @include respond-to(xs) {
                padding: 1rem 0;
                border-top: 1px solid $link;
            }

            .About-edit-div {
                flex: 1;
                display: flex;
                justify-content: end;

                @include respond-to(xs) {
                    // display: none;
                }

                .About-Pen-Div {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 30px;
                    height: 30px;
                    border-radius: 100%;
                    border: 1px solid $link;
                    cursor: pointer;
                    padding: 0;
                }
            }

            .About-icon {
                font-size: 30px;
                color: #727B87;
                margin: 1rem 0;

                .About-icon-text {
                    color: white;
                    font-size: 16px;
                    text-transform: capitalize;
                    margin: 0.5rem;
                }
            }
        }
    }
}